<template>
  <Transition name="pannel">
    <div class="pannel" @click="closeSelection">
      <div class="pannel__block" @click.stop="">
        <div class="pannel__info">
          <div class="rectangle"></div>
          <h2 class="pannel__title">{{ selectedNote.title }}</h2>
          <p class="pannel__desc">
            {{ selectedNote.desc }}
          </p>
        </div>
        <div class="pannel__btns">
          <button
            class="pannel__btn pannel__edit"
            @click="$emit('changeNote', selectedNote.id)"
          >
            <img src="../assets/images/changeMob.svg" alt="" /><span>{{
              $t("edit")
            }}</span>
          </button>
          <button
            class="pannel__btn pannel__del"
            @click="$emit('noteDel', selectedNote.id)"
          >
            <img src="../assets/images/delMob.svg" alt="" /><span>{{
              $t("del")
            }}</span>
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  props: ["note", "selectedNote"],

  data() {
    return {};
  },

  methods: {
    closeSelection() {
      this.$emit("closeSelection", false);
      // this.title = "";
      // this.desc = "";
    },
  },
};
</script>

<style>
.pannel {
  background: rgba(0, 0, 0, 0.35);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  display: flex;
  align-items: end;
  justify-content: center;
  padding-bottom: 16px;
  padding-left: 16px;
  padding-right: 16px;
}

.pannel__block {
  border-radius: 10px;
  background: #fff;
  max-width: 670px;
  width: 90%;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  align-items: center;
  position: fixed;
  padding-left: 12px;
  padding-right: 12px;
}

.rectangle {
  width: 34px;
  height: 4px;
  border-radius: 19px;
  background: #dedede;
}

.pannel__info {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  padding-top: 10px;
}

.pannel__title {
  color: #1c1b1f;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.pannel__desc {
  color: #49454f;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.25px;
  max-width: 348px;
  width: 100%;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.pannel__btns {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
}

.pannel__btns span {
  color: var(--m-3-sys-light-on-surface, #1c1b1f);
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
  letter-spacing: 0.5px;
  margin-bottom: 10px;
}

.pannel__btn {
  width: 100%;
  display: flex;
  column-gap: 16px;
}

.pannel-enter-active,
.pannel-leave-active {
  transition: 200ms linear;
}

.pannel-enter-from,
.pannel-leave-to {
  opacity: 0;
  transform: scale(1.5);
}

.pannel__edit:active span{
  color: #6750a4;
}

.pannel__del:active span{
  color: #cf1b1b;
}
</style>