<template>
  <Navbar @searchValue="search = $event" @header="header = $event" />
  <Notes
    :notes="filterNotes"
    @delNote="delNote"
    @changeNote="changeNote"
    :header="header"
    @select="select"
  />
  <Model
    v-show="modalOpen"
    @closeModal="closeModal"
    @addNote="addNote"
    :edit="edit"
    :editNote="editNote"
    @editedNote="editedNote"
  />
  <AddButton @openModal="openModal" />
  <Pannel
    v-show="selectNote"
    @closeSelection="closeSelection"
    :selectedNote="selectedNote"
    @noteDel="delNote($event)"
    @changeNote="changeNote($event)"
  />
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import Model from "@/components/Model.vue";
import AddButton from "@/components/AddButton.vue";
import Pannel from "@/components/Pannel.vue";

export default {
  components: {
    Navbar,
    Notes,
    Model,
    AddButton,
    Pannel,
  },
  data() {
    return {
      header: true,
      edit: false,
      editNote: {},
      modalOpen: false,
      notes: [],
      search: "",
      selectNote: false,
      selectedNote: {},
    };
  },
  computed: {
    filterNotes() {
      return this.search
        ? this.notes.filter((note) =>
            note.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.notes;
    },
  },
  watch: {
    notes: {
      handler(newValue) {
        // localStorage.setItem('list', JSON.stringify(this.notes))
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
  mounted() {
    this.getNotes();
  },
  methods: {
    editedNote(noteEdited) {
      this.notes.forEach((note) => {
        if (note.id == noteEdited.id) {
          note.title = noteEdited.title;
          note.desc = noteEdited.desc;
          note.date = noteEdited.date;
        }
      });
    },
    changeNote(id) {
      this.edit = this.modalOpen = true;
      let pickedNote = this.notes.find((note) => note.id == id);
      this.editNote = pickedNote;
      this.selectNote = false;
    },
    getNotes() {
      // let localNotes = localStorage.getItem('notes');
      let localNotes = localStorage.notes;
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },
    openModal() {
      this.modalOpen = true;
    },
    closeModal(status) {
      this.modalOpen = status;
      setTimeout(() => {
        this.edit = false;
      }, 500);
    },
    addNote(item) {
      this.notes.push(item);
      this.modalOpen = false;
    },
    delNote(id) {
      let index = this.notes.findIndex((note) => note.id == id);
      this.notes.splice(index, 1);
      this.selectNote = false;
    },
    select(id) {
      this.selectNote = true;
      let pickedNote = this.notes.find((note) => note.id == id);
      this.selectedNote = pickedNote;
    },
    closeSelection(status) {
      // console.log('works');
      this.selectNote = status;
      setTimeout(() => {
        this.selectNote = false;
      }, 500);
    },
  },
};
</script>

