<template>
  <Transition name="model">
    <div class="model" @click="closeModal">
      <div class="madal__block" @click.stop="">
        <div class="modal__titlebox">
          <h2 class="modal__title">
            {{ edit ? $t("changeNote") : $t("addNote") }}
          </h2>
        </div>
        <form class="modal__inputs">
          <label>
            <span>Title</span>
            <input required type="text" placeholder="Title" v-model="title" />
          </label>
          <label>
            <span>Content</span>
            <textarea required placeholder="Content" v-model="desc"></textarea>
          </label>
        </form>
        <div class="modal__btns">
          <button class="modal__btn add" v-if="!edit" @click="addNote">
            {{ $t("add") }}
          </button>
          <button class="modal__btn add" v-else @click="changeNote">
            {{ $t("change") }}
          </button>
          <button class="modal__btn cancel" @click="closeModal">
            {{ $t("cancel") }}
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { v4 as uuidv4 } from "uuid";

export default {
  props: {
    edit: Boolean,
    editNote: Object,
  },
  data() {
    return {
      title: "",
      desc: "",
    };
  },
  methods: {
    changeNote() {
      if (this.title != "" && this.desc != "") {
        const editedNote = {
          id: this.editNote.id,
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("editedNote", editedNote);
        this.closeModal();
      }
    },
    closeModal() {
      this.$emit("closeModal", false);
      this.title = "";
      this.desc = "";
    },
    addNote() {
      if (this.title != "" && this.desc != "") {
        const item = {
          id: uuidv4(),
          title: this.title,
          desc: this.desc,
          date: new Date().toLocaleDateString(),
        };
        console.log(item.id);
        this.$emit("addNote", item);
        this.title = "";
        this.desc = "";
      }
    },
  },
};
</script>

<style>
.model {
  background: rgba(0, 0, 0, 0.35);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: center;
}

.madal__block {
  border-radius: 28px;
  background: linear-gradient(
      0deg,
      rgba(103, 80, 164, 0.11) 0%,
      rgba(103, 80, 164, 0.11) 100%
    ),
    #fffbfe;
  max-width: 312px;
  width: 100%;
  padding: 24px;
}

.modal__title {
  color: #1c1b1f;
  font-size: 24px;
  font-weight: 400;
  line-height: 32px;
  margin-bottom: 16px;
}

.modal__inputs {
  display: flex;
  flex-direction: column;
  row-gap: 16px;
}

.modal__inputs label {
  position: relative;
}

.modal__inputs span {
  position: absolute;
  top: 8px;
  left: 16px;
  color: #6750a4;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;
}

.modal__inputs input,
.modal__inputs textarea {
  border-radius: 4px 4px 0px 0px;
  background: #e7e0ec;
  width: 100%;
  border: none;
  outline: none;
  resize: none;
  padding: 23px 16px 9px;
  color: #49454f;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  border-bottom: 1px solid #1c1b1f;
}

.modal__btns {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  column-gap: 8px;
  margin-top: 24px;
}

.modal__btn {
  padding: 10px 12px;
  border-radius: 5px;
  transition: 300ms linear;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.1px;
  text-transform: uppercase;
}

.cancel {
  color: #cf1b1b;
}

.add {
  color: #6750a4;
}
.cancel:hover {
  background: #ffe1e1;
}

.add:hover {
  background: #e6ddff;
}

.model-enter-active,
.model-leave-active {
  transition: 200ms linear;
}

.model-enter-from,
.model-leave-to {
  opacity: 0;
  transform: scale(1.5);
}
</style>