<template>
  <header class="header">
    <Transition name="header__notes">
      <div class="header__notes" v-show="header">
        <button class="header__lang" @click="switchLang">
          {{ $i18n.locale }}
        </button>
        <h1 class="header__title">{{ $t("notes") }}</h1>
        <button class="header__search" @click="header = false">
          <img src="@/assets/img/search.svg" alt="" />
        </button>
      </div>
    </Transition>
    <Transition name="header__form">
      <div class="header__form" v-show="header === false">
        <button class="header__back" @click="(header = true), (search = '')">
          <img src="@/assets/img/arrow.svg" alt="" />
        </button>
        <div class="container">
          <input
            v-model="search"
            type="text"
            class="header__input"
            :placeholder="$t('searchInput')"
          />
        </div>
        <button class="header__clear" @click="search = ''">
          <img src="@/assets/img/clear.svg" alt="" />
        </button>
      </div>
    </Transition>
  </header>
</template>

<script>
export default {
  data() {
    return {
      header: true,
      search: "",
    };
  },
  methods: {
    switchLang() {
      this.$i18n.locale == "ru"
        ? (this.$i18n.locale = "en")
        : (this.$i18n.locale = "ru");
      localStorage.lang = this.$i18n.locale;
    },
  },
  watch: {
    search(val) {
      this.$emit("searchValue", val);
    },
    header(val) {
      this.$emit("header", val);
    },
  },
};
</script>

<style >


button {
  border: none;
  background: transparent;
}

.header {
  background: #f3edf7;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.3),
    0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  padding: 14px 16px;
  height: 70px;
  overflow: hidden;
  z-index: 10000;
}

.header__notes,
.header__form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__lang {
  font-size: 22px;
  font-weight: 700;
  text-transform: uppercase;
}

.header__title {
  color: #1c1b1f;
  text-align: center;
  font-size: 22px;
  line-height: 28px;
}

.header__input {
  width: 100%;
  display: block;
  background: transparent;
  border: none;
  outline: none;
  color: #9d9d9d;
  font-size: 22px;
}

.header__input::placeholder {
  color: #9d9d9d;
  font-size: 16px;
  line-height: 20px;
}

.header__notes-enter-active,
.header__notes-leave-active,
.header__form-enter-active,
.header__form-leave-active {
  transition: 500ms linear;
}

.header__notes-enter-from,
.header__notes-leave-to {
  opacity: 0;
  transform: translateY(-200px);
}
.header__form-enter-from,
.header__form-leave-to {
  opacity: 0;
  transform: translateY(200px);
}

.header__notes-enter-to,
.header__notes-leave-from,
.header__form-enter-to,
.header__form-leave-from {
  opacity: 1;
  transform: translateY(0px);
}
</style>