<template>
  <div class="notes">
    <div class="container">
      <div class="notes__top">
        <h2 class="notes__title" v-if="header">
          {{ notes.length > 0 ? $t("allNotes") : $t("noNotes") }}
        </h2>
        <h2 class="notes__title" v-else>{{ $t("search") }}</h2>
        <button class="notes__btn" @click="grid = !grid">
          <img src="@/assets/img/list.svg" alt="" v-if="grid" />
          <img src="@/assets/img/grid.svg" alt="" v-else />
          <span>{{ grid ? $t("list") : $t("grid") }}</span>
        </button>
      </div>
      <div class="notes__list" :class="{ active: !grid }">
        <NoteItem
          :grid="grid"
          v-for="note in notes"
          :key="note.id"
          :note="note"
          @delNote="$emit('delNote', note.id)"
          @changeNote="$emit('changeNote', note.id)"
          @select="$emit('select', note.id)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import NoteItem from "@/components/NoteItem.vue";

export default {
  props: {
    notes: {
      typeof: Array,
      required: true,
    },
    header: {
      typeof: Boolean,
    },
  },
  components: {
    NoteItem,
  },
  data() {
    return {
      grid: false,
    };
  },
};
</script>

<style>
.notes {
  padding-top: 30px;
}

.notes__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.notes__title {
  color: #323232;
  font-size: 22px;
  line-height: 28px;
}

.notes__btn {
  border-radius: 16px;
  background: #f3edf7;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.3),
    0px 4px 8px 3px rgba(0, 0, 0, 0.15);
  cursor: pointer;
  width: 121px;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 12px;
}
.notes__btn span {
  color: #6750a4;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.1px;
}

.notes__list {
  margin-top: 30px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 24px;
  row-gap: 24px;
}

.notes__list.active {
  grid-template-columns: 1fr;
}
</style>